import React from 'react'
import Button from '@material-ui/core/Button'

function Card (props){
const style = {
  textAlign: 'center',
  border: '2px solid black',
  borderRadius: '15px',
  width: '25%',
  margin: '10px'
}
  return(
      <div style={style}>
        <h3>{props.name}</h3>
        <p>Age: {props.age}</p>
        <p>Occupation: {props.occupation}</p>
        <Button
          color="primary"
          variant="text"
          disabled
        >
          Edit
        </Button>
        <Button
          variant="text"
          color="secondary"
          onClick={() => props.removeCard(props.id)}
        >
          Remove
        </Button>
      </div>
    )
  }

export default Card;
